.login {
  width           : 100%;
  height          : 100vh;
  overflow        : hidden;
  background      : url("../imgs/home/chairs.png") center right no-repeat;
  background-size : auto 65%;

  padding         : 50px;

  article {
    max-width  : 300px;
    width      : 100%;
    text-align : center;
    h2 {
      font  : lh(310px, 50%) $lm;
      color : $default;
    }
    h3 {
      font   : lh(80px, 85%) $lm;
      margin : 0 0 40px 0;
    }

    p {
      font : lh(16px, 150%) $op;
    }

    input {
      height        : 50px;
      font-size     : 18px;
      border-radius : 0 !important;
      border        : 0 !important;
      box-shadow    : 1px 1px 2px rgba(0, 0, 0, .37) !important;
    }

  }

}